import React from 'react';
import '../../Styles/dashboard.scss';
import Axios from 'axios';

class AddNewContent extends React.Component{
  state = {
    category: false
  }
  componentDidMount() {
    Axios.get(`/api/category`).then((res) => {
      console.log(res.data);
      this.setState({
        category: res.data
      })
    }).catch((e) => {
      console.log(e)
    })
  }
  render() {
    return (
    <div className="right-content-dashboard">
      <form className="form-group">
        <input className="form-control" type="text" placeholder="Enter Your Title..."/>
        <textarea className="form-control"  col="100%" rows="5" placeholder="Enter Your Description"></textarea>
        <select className="form-control" >
          <option value="-1">Select a category</option>
          {this.state.category ? 
          this.state.category.map((category) => {
            return <option key={category._id} value={category._id}>{category.name}</option>
          })
          : <option value="-1">Loading...</option>}
        </select>
        Upload Your File
        <input type="file"/>
        <input className="btn btn-blue"  type="submit" defaultValue="Submit"/>
      </form>
    </div>
    )
  }
}

export default AddNewContent;